.cities {
    width: 95%;
    margin: auto;
    h3 {
        text-decoration: underline;
    }
    .articlesContainer {
        display: grid;
        grid-template-rows: auto;
        grid-template-columns: repeat(auto-fit, minmax(20em, 1fr));
        column-gap: 0.2em;
        row-gap: 0.2em;
        justify-self: center;
        margin: 0.2em auto;
        .articleCard {
            justify-self: stretch;
            border: black solid 0.1em;
            padding-bottom: 0.5em;
            .articleCardText {
                padding: 0.1em 0.5em;
                .blockContainer {
                    display: grid;
                    grid-template-columns: auto 1fr;  
                    .block1 {
                        a{
                            color: black;
                            text-decoration: none;
                            &:hover {
                                text-decoration: underline;
                            }
                        }
                    }
                    .block2 {
                        p {
                            text-indent: 0.2em;
                        }
                    }
                }
                div {
                    .block1 {
                        a{
                            color: black;
                        }
                    }
                    .block2 {
                        p {
                            text-indent: 0.2em;
                        }
                    }
                }
                p {
                    font-size: 1em; 
                }
            }
            .articleImage {
                height: 16em;
                margin: auto;
                background-repeat: no-repeat;
                background-size: cover;
                background-position: center;
            }
            .city1 {
                background-image: url(../../../Images/cityLookUp.jpg);
            }
            .city2 {
                background-image: url(../../../Images/cityCanal.jpg);
            }
            .city3 {
                background-image: url(../../../Images/mistyCity.jpg);
            }
            .city4 {
                background-image: url(../../../Images/adelaide.jpg);
            }
            .city5 {
                background-image: url(../../../Images/urbanWorld.png);
            }
            .city6 {
                background-image: url(../../../Images/solarPanelNight.jpg);
            }
            .city7 {
                background-image: url(../../../Images/birdsEyeCityLights.jpg);
            }
            .city8 {
                background-image: url(../../../Images/colourfulPark.jpg);
            }
        }
    }
}